import React, {Component} from 'react'
import {Link} from 'react-router-dom'
import { Swipeable } from 'react-swipeable'
import {AppBar,Toolbar,IconButton,Typography, Drawer, List, ListItem, ListItemText} from '@material-ui/core'
import TreeView from '@material-ui/lab/TreeView';
import TreeItem from '@material-ui/lab/TreeItem';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import MoreHorizIcon from '@material-ui/icons/MoreHoriz'
import ArrowForwardIcon from '@material-ui/icons/ArrowForward'
import ArrowBackIcon from '@material-ui/icons/ArrowBack'
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos'
import ZoomInIcon from '@material-ui/icons/ZoomIn'
import ZoomOutIcon from '@material-ui/icons/ZoomOut'
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
class View extends Component{
    constructor(props) {
        super(props);
        this.state = {
            page:1,
            zoom:1,
            isOpenMenu:false,
            isOpenZmist:false,
        }
        this.nextPage=this.nextPage.bind(this)
        this.prevPage=this.prevPage.bind(this)
        this.zoomIn=this.zoomIn.bind(this)
        this.zoomOut=this.zoomOut.bind(this)
        this.openMenu=this.openMenu.bind(this)
        this.closeMenu=this.closeMenu.bind(this)
        this.openZmist=this.openZmist.bind(this)
        this.closeZmist=this.closeZmist.bind(this)
    }
    componentDidMount(){
        this.props.getObjectView(this.props.match.params.id)
        this.props.setViewMode(true)
    }
    componentWillUnmount(){
        this.props.setViewMode(false)
    }
    nextPage(){
        if(this.state.page<this.props.object.pages.length-1)
            this.goToPage(this.state.page+1)
        else
            this.goToPage(this.props.object.pages.length-1)
    }
    prevPage(){
        this.goToPage(this.state.page-1)
    }
    zoomIn(){
        this.setState({
            zoom:this.state.zoom+0.1
        })
    }
    zoomOut(){
        if(this.state.zoom>=1.1)
        {
            this.setState({
                zoom:this.state.zoom-0.1
            })
        }
    }
    goToPage(page){
        if(page<1)
            page=1
        this.setState({page:page})
    }
    openMenu(){
        this.setState({
            isOpenMenu:true
        })
    }
    closeMenu(){
        this.setState({
            isOpenMenu:false
        })
    }
    openZmist(){
        this.setState({
            isOpenMenu:false,
            isOpenZmist:true,
        })
    }
    closeZmist(){
        this.setState({
            isOpenZmist:false,
        })
    }
    render(){
        return(<>
            <AppBar position="fixed">
                <Toolbar>
                    <Link style={{color:'#fff'}}to={'/object/'+this.props.match.params.id}>
                        <IconButton edge="start" className="menu-button" color="inherit" aria-label="menu">
                        <ArrowBackIosIcon onClick={this.zoomIn}/>
                    </IconButton>
                    </Link>
                    <Typography variant="h6" className="menu-title">
                        Культура України
                    </Typography>
                    <IconButton edge="start" className="menu-button" color="inherit" id='menuButton' onClick={this.openMenu}>
                        <MoreHorizIcon/>
                    </IconButton>
                    <Menu
                        id="simple-menu"
                        anchorEl={document.getElementById('menuButton')}
                        keepMounted
                        open={this.state.isOpenMenu}
                        onClose={this.closeMenu}
                      >
                        <MenuItem onClick={this.openZmist}>Зміст</MenuItem>
                    </Menu>
                </Toolbar>
            </AppBar>
            <div className="page-view-control">
                <IconButton edge="start" className="menu-button" color="inherit" aria-label="menu">
                    <ZoomInIcon onClick={this.zoomIn}/>
                </IconButton>
                <div className="controls">
                    <IconButton edge="start" className="menu-button" color="inherit" aria-label="menu">
                        <ArrowBackIcon onClick={this.prevPage}/>
                    </IconButton>
                    <span className="page-view-control-pages">
                        {this.state.page+" / "+(this.props.object.pages ? this.props.object.pages.length-1 : "")}
                    </span>
                    <IconButton edge="start" className="menu-button" color="inherit" aria-label="menu">
                        <ArrowForwardIcon onClick={this.nextPage}/>
                    </IconButton>
                </div>
                <IconButton edge="start" className="menu-button" color="inherit" aria-label="menu">
                    <ZoomOutIcon onClick={this.zoomOut}/>
                </IconButton>
            </div>
            <div className="page-view">
                <Swipeable onSwipedLeft={()=>{if(this.state.zoom<=1){this.nextPage()}}} onSwipedRight={()=>{if(this.state.zoom<=1)this.prevPage()}} onSwipedDown={()=>{}} onSwipedUp={()=>{}}>
                    <img style={{width:(100*this.state.zoom)+"%"}} src={"http://elib.nlu.org.ua/"+this.props.object.path+"/jpg/"+(this.props.object.pages ? this.props.object.pages[this.state.page] : "")}/>
                </Swipeable>
            </div>

            <Drawer className="left-menu" anchor="left" open={this.state.isOpenZmist} onClose={this.closeZmist}>
                <TreeView
                  style={{width:'70vw'}}
                  defaultCollapseIcon={<ExpandMoreIcon />}
                  defaultExpandIcon={<ChevronRightIcon />}
                >
                  <TreeItem nodeId="1" label="Applications">
                    <TreeItem nodeId="2" label="Calendar" />
                    <TreeItem nodeId="3" label="Chrome" />
                    <TreeItem nodeId="4" label="Webstorm" />
                  </TreeItem>
                  <TreeItem nodeId="5" label="Documents">
                    <TreeItem nodeId="10" label="OSS" />
                    <TreeItem nodeId="6" label="Material-UI">
                      <TreeItem nodeId="7" label="src">
                        <TreeItem nodeId="8" label="index.js" />
                        <TreeItem nodeId="9" label="tree-view.js" />
                      </TreeItem>
                    </TreeItem>
                  </TreeItem>
                </TreeView>
            </Drawer>
         </>
        )
    }
}
export default View
