import React, {Component} from 'react'
import BookCard from './BookCard'
import {Link} from 'react-router-dom'
import {AppBar,Toolbar,IconButton,Typography,Button,Card,CardContent,CardActions,CardMedia} from '@material-ui/core'
import BreadCrumbs from './BreadCrumbs'
class CollectionList extends Component{
    componentDidMount(){
        console.log(this.props.match.params.id)
        this.props.getCollection(this.props.match.params.id)
    }
    componentDidUpdate(prevProps){
        if(this.props.match.params.id!==prevProps.match.params.id)
        {
            this.props.getCollection(this.props.match.params.id)
        }
    }
    render(){
        return(<>
            <BreadCrumbs breadcrumbs={this.props.collections.parent}/>
            <center><h3>{this.props.collections.title}</h3></center>
            {this.props.collections.items ?
                (this.props.collections.items.map((item)=>
                    <Link key={item.id} to={(item.type==="book" ? '/object/' : '/collection/') + item.id}>
                        <BookCard
                            cover={"https://elib.nlu.org.ua/pic.php?w=93&h=97&pic="+(item.type==="book" ? item.path :"resourses/collections/"+item.id)+"/cover.jpg"}
                            title={item.title}
                            id={item.id}
                        />
                    </Link>
                )):(<></>)}
         </>
        )
    }
}
export default CollectionList;
