import React, {Component} from 'react'
import {Link} from 'react-router-dom'
import Parser from 'html-react-parser'
import {AppBar,Toolbar,IconButton,Typography,Button,Card,CardContent,CardActions,CardMedia} from '@material-ui/core'
import BreadCrumbs from './BreadCrumbs'
class Book extends Component{
    componentDidMount(){
        this.props.getObject(this.props.match.params.id)
    }
    render(){
        console.log(this.props)
        return(<>
            <BreadCrumbs breadcrumbs={this.props.object.parent}/>
            <center><h3>{this.props.object.title}</h3></center>
            <center>
            <div class="ObjImg">
                <Link to={"/view/"+this.props.object.id}>
                    <img src={"https://elib.nlu.org.ua/pic.php?w=152&h=198&pic="+this.props.object.path+"/cover.jpg"}/>
                </Link>
            </div>
            </center>
            <div class='book-description'>
                {this.props.object.description}
            </div>
            <div className="ObjBibOpus">
                <b>Бібліографічний опис:</b><br/>
                {typeof this.props.object.metadata==='string' ? Parser(this.props.object.metadata) : ""}
            </div>
         </>
        )
    }
}
export default Book;
