var initialState={
    collections:[],
    object:{},
    viewMode:false
}
export default function (state, action) {
    if(!state)
        state=initialState
    switch(action.type){
        case 'GET_COLLECTION_SUCCESS':
            return {
                ...state,
                collections:action.payload.data
        }
        case 'GET_OBJECT_SUCCESS':
            return {
                ...state,
                object:action.payload.data
        }
        case 'GET_OBJECT_VIEW_SUCCESS':
            return {
                ...state,
                object:action.payload.data
        }
        case 'SET_VIEW_MODE':
            return{
                ...state,
                viewMode: action.value
            }
        default :
            return state
    }
}

