import React, {Component} from 'react';
import {AppBar,Toolbar,IconButton,Typography,Button,Card,CardContent,CardActions,CardMedia} from '@material-ui/core'
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import {Link} from 'react-router-dom';

class App extends Component{
    constructor(props) {
        super(props);
        this.state = {
            isOpenMenu:false
        }
        this.toggleMenu=this.toggleMenu.bind(this)
    }
    toggleMenu(){
        this.setState({
                isOpenMenu:!this.state.isOpenMenu
        })
    }
    render(){
        return (<>
            {!this.props.main.viewMode ?
            <AppBar position="fixed">
                <Toolbar>
                    <IconButton edge="start" className="menu-button" color="inherit" aria-label="menu">
                    <MenuIcon onClick={this.toggleMenu}/>
                    </IconButton>
                    <Typography variant="h6" className="menu-title">
                        Культура України
                    </Typography>
                    <IconButton edge="start" className="menu-button" color="inherit">
                        <SearchIcon/>
                    </IconButton>
                </Toolbar>
            </AppBar> : <></>
            }
            <Drawer className="left-menu" anchor="left" open={this.state.isOpenMenu} onClose={this.toggleMenu}>
                <List>
                    <Link to={"/"}>
                        <ListItem className="left-menu-item" onClick={this.toggleMenu}>
                            <ListItemText primary="На головну"/>
                        </ListItem>
                    </Link>
                </List>
            </Drawer>
            {this.props.children}
        </>);
    }
}

export default App;
